import { $, $All } from '../util/Helpers';

class Plans{
    constructor(helpers){
        this.helpers = helpers;
    }
    init(helpers){
        const els = {
            stickyNav: $('.fixed-nav'),
            stickyNavCover: $All('.fixed-nav__cover'),
            frontCover: $(".fixed-nav__cover--front"),
            submitCover: $(".fixed-nav__cover--submit"),
            open: $All("[data-open=fixedNav]"),
            close: $All("[data-close=fixedNav]"),
            header: $(".header"),
            aboutBtn: $(".submit-box__submit")
        }
        const className = { active: 'active', clicked: 'clicked' }
        const eachCoverEls = [ els.submitCover, els.frontCover ];
        this.setEvent(helpers, els, className, eachCoverEls);
    }
    setEvent(helpers, els, className, eachCoverEls){
        // const target = {
        //     headerBottom: els.header.offsetHeight,
        //     fixPoint: els.aboutBtn.offsetTop + els.aboutBtn.offsetHeight
        // }
        helpers.on(window, 'scroll', _ =>{
            this.setScrollEvent(els, window.scrollY, className.active, target.fixPoint, target.headerBottom)
        })
        helpers.on(els.open, 'click', _ => {
            return helpers.addClass(eachCoverEls, className.clicked);
        })
        helpers.on(els.close, 'click', _ => {
            return helpers.removeClass(eachCoverEls, className.clicked);
        })
    }
    setScrollEvent(els, currentTop, className, fixPoint, headerBottom){
        this.showStickNav([...els.stickyNavCover, els.stickyNav], fixPoint, currentTop, className);
        this.hideStickyNav(els.stickyNavCover, fixPoint, currentTop, className);
        this.hideStickyNav(els.stickyNav, headerBottom, currentTop, className);
    }
    showStickNav(el, target, currentTop, className){
        if(currentTop > target) this.helpers.addClass(el, className);
    }
    hideStickyNav(el, target, currentTop, className){
        if(currentTop < target) this.helpers.removeClass(el, className);
    }
}


export {Plans};






























